import React, { Suspense, lazy } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';

import { Bullseye, Spinner } from '@patternfly/react-core';

const OopsPage = lazy(() =>
  import(/* webpackChunkName: "OopsPage" */ './Routes/OopsPage/OopsPage')
);
const NoPermissionsPage = lazy(() =>
  import(
    /* webpackChunkName: "NoPermissionsPage" */ './Routes/NoPermissionsPage/NoPermissionsPage'
  )
);
const AuthFactors = lazy(() => 
  import(
    /* webpackChunkName: "Authentication-Factors"*/ './Routes/Authentication-Policy/Authentication-Factors'
  )
);

/**
 * the Switch component changes routes depending on the path.
 *
 * Route properties:
 *      exact - path must match exactly,
 *      path - https://prod.foo.redhat.com:1337/insights/advisor/rules
 *      component - component to be rendered when a route has been chosen.
 */
export const Routes = () => (
  <Suspense
    fallback={
      <Bullseye>
        <Spinner />
      </Bullseye>
    }
  >
    <Switch>
      <Route path="/oops" component={OopsPage} />
      <Route path="/no-permissions" component={NoPermissionsPage} />
      <Route path="/authentication-factors" component={AuthFactors}/>
      {/* Finally, catch all unmatched routes */}
      <Route>
        <Redirect to="/authentication-factors" />
      </Route>
    </Switch>
  </Suspense>
);
